'use client';

import { useEffect, useState } from 'react';
import NewsCarousel from '@/components/NewsCarousel';

type Article = {
  title: string;
  description?: string;
  url?: string;
  source?: string;
  urlToImage?: string;
  publishedAt?: string;
};

export default function NoticiasPage() {
  const [articles, setArticles] = useState<Article[]>([]);
  const [loading, setLoading] = useState(true);
  const [searchQuery, setSearchQuery] = useState('');

  useEffect(() => {
    fetch('/api/news')
      .then(async (res) => {
        if (!res.ok) {
          const err = await res.json().catch(() => ({}));
          throw new Error(err?.error || 'Error al cargar noticias');
        }
        return res.json();
      })
      .then((data) => {
        const raw = data.articles || [];
        // üîπ Normalizamos los art√≠culos igual que en Dashboard
        const normalized = raw.map((a: any) => ({
          title: a.title || '',
          description: a.description || '',
          url: a.url || '',
          source: (a.source && (a.source.name || a.source)) || a.source || '',
          urlToImage: a.urlToImage || a.image || '',
          publishedAt: a.publishedAt || ''
        }));
        setArticles(normalized);
      })
      .catch((err) => console.error('fetch /api/news error', err))
      .finally(() => setLoading(false));
  }, []);

  // Filtrar art√≠culos por t√≠tulo
  const filteredArticles = articles.filter((article) =>
    article.title.toLowerCase().includes(searchQuery.toLowerCase())
  );

  if (loading) return <div>Cargando noticias...</div>;

  return (
    <main style={{ padding: '16px', position: 'relative' }}>
      <h1 style={{ textAlign: 'center', marginBottom: '16px' }}>Secci√≥n de Noticias</h1>

      {/* Barra de b√∫squeda */}
      <div style={{ textAlign: 'center', marginBottom: '16px' }}>
        <input
          type="text"
          placeholder="Buscar noticias..."
          value={searchQuery}
          onChange={(e) => setSearchQuery(e.target.value)}
          style={{
            width: '80%',
            maxWidth: '400px',
            padding: '8px 12px',
            borderRadius: '4px',
            border: '1px solid #ccc'
          }}
        />
      </div>

      {/* Botones centrados */}
      <div
        style={{
          position: 'relative',
          display: 'flex',
          justifyContent: 'center',
          gap: '1rem',
          marginBottom: '16px'
        }}
      >
        <button
          style={{
            padding: '10px 20px',
            backgroundColor: '#6b5b95',
            color: 'white',
            border: 'none',
            borderRadius: '5px',
            cursor: 'pointer'
          }}
          onClick={() => console.log('Bot√≥n 1 clic')}
        >
          Bot√≥n 1
        </button>
        <button
          style={{
            padding: '10px 20px',
            backgroundColor: '#feb236',
            color: 'white',
            border: 'none',
            borderRadius: '5px',
            cursor: 'pointer'
          }}
          onClick={() => console.log('Bot√≥n 2 clic')}
        >
          Bot√≥n 2
        </button>
      </div>

      {/* Noticias */}
      {filteredArticles.length > 0 ? (
        <NewsCarousel articles={filteredArticles} />
      ) : (
        <p style={{ textAlign: 'center' }}>No se encontraron noticias.</p>
      )}
    </main>
  );
}
